<template>
  <div id="app">
    <menuTop class="menuTop"/>
    <transition name="fade">
      <router-view :newsList='newsList' class="content"/>
    </transition>
    <footerBom />
  </div>
</template>

<script>
import menuTop from "@/components/menu.vue"
import footerBom from "@/components/footer.vue"
export default {
  name: 'App',
  components: {
    menuTop,footerBom
