  data () {
    return {
        newsList: [
          {updated_at: '2024/04/29',title: 'Please consider to submit your work to the MIMSVAI 2024 workshop (in conjunction with UbiComp/ISWC 2024)',content: 'https://mimsvai.github.io'},
          {updated_at: '2023/08/31',title: 'Full program of the MIMSVAI 2023 workshop (in conjunction with UbiComp/ISWC 2023) is out. Please consider to register in the MIMSVAI 2023 workshop',content: 'https://mimsvai.github.io'},
          {updated_at: '2023/08/01',title: 'Prof. Chuang-Wen You has been promoted to Associate Professor',content: ''},
          {updated_at: '2022/11/30',title: 'TechLifeProbe system was exhibited in the Healthcare+ Expo 2022',content: ''},
          {updated_at: '2022/08/25',title: 'Prof. Chuang-Wen You received 2022 NTHU 25th New Faculty Research Award',content: ''},
          {updated_at: '2022/07/06',title: 'Please consider to submit your work to the MIMSVAI 2022 workshop (in conjunction with UbiComp/ISWC 2022)',content: ''},
          {updated_at: '2022/05/18',title: 'Prof. Chuang-Wen You won the Second Place Prize Paper Award for Drunk Driving Prevention in Taiwan Association against the Drunk Driving (TADD)',content: ''},
          {updated_at: '2021/12/22',title: 'The website of UbiComp Lab at National Tsing Hua University is up and running!!!',content: ''}
        ]
      }
    }
}
</script>

<style>
body {
  margin: 0;
}
.content {
  min-height: 90vh;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



