<template lang="pug">
#SlideShow
    transition(name="fade")
        #sliceshowContainer(:style="{'background-image': 'url('+pages[idx].image+')'}" :key="idx")
            .sectionContainer(v-if="pages[idx] && bannerShow" @click="changePath(pages[idx].link)")
                .sectionTitle {{pages[idx].title}}
                .sectionText {{pages[idx].content}}
    .slideDotContainer
        .slideDot(v-for="dotIdx in pagesNum" :class="dotIdx==idx+1?'activite':''")
</template>

<script>
var slide;
export default {
    data () {
        return{
            idx: 0,
            isLoad: false,
            bannerShow: true,
            pages: [
                {
                    image:require('@/assets/banner/soberbot_banner.png'),
                    title:'Soberbot',
                    content:"Developing systems to improve alcohol use disorder through chatbot technology.",
                    link:'/projects/post/soberbot'
                },
                {
                    image:require('@/assets/banner/tech_abuse_banner.png'),
                    title:'TechLifeProbe',
                    content:"Using mobile and wearable technologies to improve adolescents' smartphone addiction through sharing personal data with their parents.",
                    link:'/projects/post/tech_abuse'
                },
                {
                    image:require('@/assets/banner/cravingprobe_banner.png'),
                    title:'CravingProbe',
                    content:"Incorporating virtual reality (VR) and biofeedback technologies to assist psychotherapists in raising patients' mind-body awareness and correct attributions of craving stimuli in psychotherapy.",
                    link:'/projects/post/vr-drug'
                },
                {
                    image:require('@/assets/banner/gambling_addiction_banner.png'),
                    title:'Computational Support for reducing problem gambling',
                    content:"Using mobile and wearable technologies to assist in recovering from gambling addiction.",
                    link:'/projects/post/gambling_addiction'
                },
                {
                    image:require('@/assets/banner/shopping_addiction_banner.jpg'),
                    title:'Social Sening Support for reducing shopping gambling',
                    content:"Developing systems to improve shopping addiction through social sensing technology.",
                    link:'/projects/post/shopping_addiction'
                }
            ],
            pagesNum: 4,
        }
    },
    methods: {
        init() {
            this.pagesNum = this.pages.length;
            slide = setInterval(() => {
                this.bannerShow = false;
                if(this.idx<this.pagesNum-1){
                    this.idx+=1;
                }
                else{
                    this.idx=0;
                }
                // this.$refs.slideshow.goTo(this.idx);
            }, 6000);
        },
        changePath: function(src){
            this.$router.push({ path: src})
        },
    },
    watch: {
        'idx': function(){
            this.bannerShow = true;
        }
    },
    created() {
        this.init();
        this.isLoad = true;
    },
    beforeDestroy(){
        clearInterval(slide);
    }
}
</script>

<style lang="sass" scoped>
#SlideShow
    padding: 40px 0 20px 0
#sliceshowContainer
    position: absolute
    height: 400px
    background-size: cover
    background-position: left
    background-repeat: no-repeat
    border-radius: 16px
    @media all and (min-width: 1480px)
        left: 50%
        width: 60%
        transform: translate(-50%, 0)
    @media all and (max-width: 1480px)
        width: 80%
        left: 10%
    @media all and (max-width: 746px)
        width: 100%
        left: 0%
        background-position: center
.sectionContainer
    height: fit-content
    position: relative
    background-color: #0007
    color: #FFF
    border-radius: 16px
    padding: 20px
    cursor: pointer
    transition: 0.4s
    .sectionTitle
        font-size: 24px
        letter-spacing: 2px
        font-weight: 300
    .sectionText
        border-left: 2px #FFDF6C solid
        padding: 10px
        margin-top: 10px
    @media all and (min-width: 746px)
        width: 25%
        left: 67.5%
        top: 25%
    @media all and (max-width: 746px)
        width: 80%
        left: 5%
        top: 25%
.sectionContainer:hover
    background-color: #000a
.slideDotContainer
    position: absolute
    margin: 20px auto
    width: fit-content
    display: flex
    top: 520px
    left: 50%
    transform: translate(-50%, 0)
    .slideDot
        width: 10px
        height: 10px
        border-radius: 10px
        border: 1px #707070 solid
        margin: 0 5px
    .activite
        background-color: #707070

</style>



